<template>
  <div>
    <router-link
      :to="{
        path: '/solutionInfo',
        name: 'solutionInfo',
        params: {
          id: dataListId,
        },
      }"
    >
      <div class="listBox">
        <div>
          <van-image width="154" height="94" :src="imageUrl"></van-image>
          <div>{{ title }}</div>
        </div>
        <div class="listBoxText">{{ content }}</div>
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  props: {
    title: String,
    content: String,
    imageUrl: String,
    dataListId:Number
  },
};
</script>

<style lang="scss" scoped>
.listBox {
  border-radius: 8px;
  display: flex;
  background-color: #fff;
  padding: 12px;
  margin: 10px 18px;
  .listBoxText {
    margin-left: 8px;
    width: 60%;
    text-indent: 2em;
    font-size: 12px;
    color: #666666;
  }
}
</style>



